<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
        <v-spacer></v-spacer>
        <v-toolbar-title>Inscripcin de Estudiante</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-alert
        class="mt-0 mb-3"
        :value="true"
        :type="msg.estado === 'Aprobado' ? 'success' : 'error'"
      ><h3>{{ `Ultimo curso: Libro ${msg.libro}` }}</h3> <h3>{{ msg.estado }}</h3></v-alert>
      <update-user
        :form="form"
        :select_items="list_enabled"
        :update="false"
        :disable="true"
        v-on:createRegister="createRegister()"
      ></update-user>
    </v-flex>
  </v-layout>
</template>

<script>
import UpdateUser from "@/components/Form/UpdateUser.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      names: "",
      lastname: "",
      ci: "",
      mobile: "",
      email: "",
      genre: "",
      msg: {
        estado: "",
        libro: ""
      },
      id: null,
      course_id: null
    };
  },

  computed: {
    ...mapGetters({
      list_enabled: "book/list_enabled"
    }),
    form() {
      return {
        nombres: this.names + " " + this.lastname,
        correo: this.email,
        ci: this.ci,
        cel: this.mobile,
        estudiante_id: this.id,
        curso_id: this.course_id
      };
    }
  },

  methods: {
    ...mapActions({
      enrollStudent: "student/enrollStudent",
      fetchBooks: "book/fetchBooks",
      fetchCourses: "book/fetchCourses"
    }),
    resetForm() {
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
      this.$validator.reset();
    },

    createRegister() {
      this.enrollStudent({
        curso_id: this.form.curso_id,
        estudiante_id: this.form.estudiante_id
      }).then(() => {
        this.$router.push({ name: "list-students" });
      });
    }
  },
  mounted() {
    if (this.$route.params.id) {
      this.fetchBooks().then(() => {
        this.fetchCourses();
      });
      this.names = this.$route.params.nombres;
      this.lastname = this.$route.params.apellidos;
      this.email = this.$route.params.correo;
      this.ci = this.$route.params.ci;
      this.mobile = this.$route.params.cel;
      this.id = this.$route.params.id;
      this.msg.libro = this.$route.params.libro;
      this.msg.estado = this.$route.params.estado;
    } else {
      this.$router.push({ name: "search-student" });
    }
  }
};
</script>

