<script>
import { mapGetters, mapActions } from "vuex";
import UpdateUser from "@/components/Form/UpdateUser.vue";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      names: "",
      lastname: "",
      email: "",
      address: "",
      phone: "",
      mobile: "",
      genre: "",
      branch_office: "",
      user: null
    };
  },
  computed: {
    ...mapGetters({
      branch_offices: "branch_office/branch_offices",
      administrators: "administrator/administrators"
    }),
    form() {
      return {
        nombres: this.names,
        apellidos: this.lastname,
        correo: this.email,
        dir: this.address,
        telf: this.phone,
        cel: this.mobile,
        sexo: this.genre,
        sucursal_id: this.branch_office
      };
    }

    // changed() {
    //     return Object.keys(this.cambios).length > 0;
    // }
  },
  methods: {
    ...mapActions({
      fetchBranchOffices: "branch_office/fetchBranchOffices",
      fetchAdministrators: "administrator/fetchAdministrators",
      updateAdministrator: "administrator/updateAdministrator"
    }),

    cambios() {
      let data = {};
      if (Object.keys(this.form).length > 0) {
        Object.keys(this.form).forEach(key => {
          if (!!this.form[key] && this.form[key] !== this.user[key]) {
            data[key] = this.form[key];
          }
        });
      }
      return data;
    },

    updateRegister() {
      console.log("Aqui va el Axios");
      this.updateAdministrator({
        id: this.$route.params.id,
        datos: this.cambios()
      }).then(() => {
        //   this.resetForm();
        console.log('Se hizo correctamente');
        
      });
    },

    validateBeforeSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) {
            // fallan las validaciones
            // console.log('Algo anda mal', result);
          } else {
            console.log(this.cambios());

            if (Object.keys(this.cambios).length > 0) {
              this.updateAdministrator({
                id: this.$route.params.id,
                datos: this.cambios
              }).then(() => {
                //   this.resetForm();
              });
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    this.fetchBranchOffices();
    this.fetchAdministrators().then(() => {
      const id = Number(this.$route.params.id);
      const user = this.administrators.find(admin => admin.id === id);
      this.user = user;

      this.names = user.nombres;
      this.lastname = user.apellidos;
      this.email = user.correo;
      this.address = user.dir;
      this.phone = user.telf;
      this.mobile = user.cel;
      this.branch_office = user.sucursal_id;
      this.genre = user.sexo;
      // console.log(this.administrators.find(admin => admin.id === this.$route.params.id));
    });
  }
};
</script>

