/* Axios */
import { axios } from "./../../main";

const auth = {
    namespaced: true,

    state: {
        user: null,
        role: null,
        logged: !!localStorage.getItem('_token')
    },

    getters: {
        user: (state) => state.user,
        role: (state) => state.role,
        logged: (state) => state.logged
    },

    actions: {
        login: ({ commit }, userInput) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.post('login', userInput)
                    .then(data => {
                        if (data.data.code === 200) {
                            localStorage.clear();
                            localStorage.setItem('_token', data.data.content.token);
                            localStorage.setItem('_user', JSON.stringify(data.data.content.data.user));
                            commit('setUser');
                        }

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', !!data.data.content, { root: true });


                        resolve(data);
                    })
                    .catch(err => {
                        console.log(err);
                        reject(err);
                    })
                    .then(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            })
        },

        updateProfile: ({ state, commit }, userInput) => {
            commit('global/startProcessing', null, { root: true });

            return new Promise((resolve, reject) => {
                let data;
                switch (state.role) {
                    case 'root':
                        data = {
                            datos: userInput
                        };
                        break;
                    case 'admin':
                        data = { id: state.user.id, datos: userInput }
                        break;
                    case 'docente':
                        data = { id: state.user.id, datos: userInput }
                        break;

                    default:
                        break;
                }
                axios.put(`${state.role}/`, data)
                    .then((data) => {
                        if (data.data.code === 200) {
                            localStorage.setItem('_token', data.data.content.token);
                            if (state.role === 'root') {
                                localStorage.setItem('_user', JSON.stringify(data.data.content.data.user));
                                commit('setUser');
                            }
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        // console.log(data);

                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        logout: ({ commit }) => {
            localStorage.removeItem('_token');
            localStorage.removeItem('_user');
            localStorage.clear();
            commit('setUser');
        }
    },

    mutations: {
        setUser: (state) => {
            if (localStorage.getItem('_token')) {
                // const token = localStorage.getItem('_token');
                // const jwt_decode = require('jwt-decode');
                // state.user = jwt_decode(token);
                state.user = JSON.parse(localStorage.getItem('_user'));
                if (state.user.tipo === 'administrativo') {
                    state.role = 'admin';
                } else {
                    state.role = state.user.tipo;
                }
                state.logged = true;
            } else {
                state.logged = false;
                state.role = null;
                state.user = null;
            }
        },

        setLogged: (state, logged) => {
            state.logged = logged;
        },

        setRole(state, role) {
            state.role = role
        },
    }
};

export default auth;