<template>
  <v-layout justify-center>
    <v-flex xs12 sm12 md12 lg12>
      <v-expansion-panel popout>
        <v-expansion-panel-content v-for="(item,i) in students" :key="i">
          <div slot="header">{{ `Libro ${item.nro}` }}</div>
          <v-card>
            <users-list
              :item_list="item.estudiantes"
              :headers="headers"
              :with_props="true"
              v-on:removeUser="removeStudent($event)"
              :title="`Estudiantes del nivel libro ${item.nro}`"
              path="update-student"
            ></users-list>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import UsersList from "@/components/List/UsersList.vue";
export default {
  components: {
    UsersList
  },
  data() {
    return {
      key: ""
    };
  },
  computed: {
    ...mapGetters({
      students: "student/students",
      headers: "student/headers"
    })
  },
  methods: {
    ...mapActions({
      fetchStudents: "student/fetchStudents",
      deleteStudent: "student/deleteStudent"
    }),
    removeStudent(id) {
      this.$swal({
        title: "Ests seguro?",
        text: "",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "S, hazlo!"
      }).then(result => {
        if (result.value) {
          this.deleteStudent(id).then(() => {
          });
        }
      });
    }
  },

  mounted() {
    this.fetchStudents();
  }
};
</script>

