<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
      <v-spacer></v-spacer>
      <!-- <v-icon>person</v-icon> -->
      <v-toolbar-title>Actualizar Nota Mnima del Nivel</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
      <update-user
        :form="form"
        :cambios="cambios"
        :update="true"
        v-on:updateRegister="updateRegister()"
      ></update-user>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import UpdateUser from "@/components/Form/UpdateUser.vue";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      minimum_note: "",
      level: null
    };
  },
  computed: {
    ...mapGetters({
      levels: "level/levels",
    }),
    form() {
      return {
        notamin: this.minimum_note,
      };
    }
  },
  methods: {
    ...mapActions({
      fetchLevels: "level/fetchLevels",
      updateLevel: "level/updateLevel"
    }),

    cambios() {
      let data = {};
      if (Object.keys(this.form).length > 0) {
        Object.keys(this.form).forEach(key => {
          if (!!this.form[key] && this.form[key] !== this.level[key]) {
            data[key] = this.form[key];
          }
        });
      }
      return data;
    },

    updateRegister() {
      console.log("Aqui va el Axios");
      this.updateLevel({
        id: this.$route.params.id,
        datos: this.cambios()
      }).then(() => {
        //   this.resetForm();
        this.$router.push({name: 'list-levels'})
      });
    },
  },
  mounted() {
    this.fetchLevels().then(() => {
      const id = Number(this.$route.params.id);
      const level = this.levels.find(level => level.id === id);
      this.level = level;

      this.minimum_note = level.notamin;
    });
  }
};
</script>

