<template>
  <v-card flat class="secondary">
    <v-card-title>
      <h2>{{ title }}</h2>
      <v-spacer v-if="action"></v-spacer>
      <v-btn
        @click="$emit('show_dialog')"
        color="success"
        dark
        block
        class="elevation-0"
        v-if="title === 'Habilitados'"
      >
      <v-icon left>{{ title === 'Habilitados' ? 'library_add' : 'check_circle_outline'}}</v-icon>{{ action }}
      </v-btn>
      <v-spacer v-if="search !== null"></v-spacer>
      <v-text-field
        v-if="search !== null"
        v-model="query"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table color="transparent" :headers="headers" :items="item_list" :search="query">
      <template color="transparent" slot="items" slot-scope="props">
        <td
          align-self-end
          class="text--center"
          v-for="(key, index) in headers"
          v-if="key.text !== 'Acciones'"
          :key="index"
          d-flex
        >
          <span
            v-if="key.value !== 'libro' && key.value !== 'admin' && key.value !== 'habilitado'"
          >{{ props.item[key.value] }}</span>
          <span v-else-if="key.value === 'libro'">{{ props.item[key.value] === null ? '' :`${props.item[key.value].nro}` }}</span>
          <span
            v-else-if="key.value === 'admin' && props.item[key.value] !== null"
          >{{ `${props.item[key.value].nombres} ${props.item[key.value].apellidos}` }}</span>
          <!-- <span v-else-if="key.value === 'habilitado'" d-flex>
            <v-chip
              disabled
              :color="props.item[key.value] === 1 ? 'success' : 'red'"
              text-color="white"
            >{{ props.item[key.value] === 1 ? 'Habilitado' : 'Inhabilitado' }}</v-chip>
          </span> -->
        </td>
        <td v-if="!card_action">
          <v-btn
            class="mr-0"
            v-if="title !== 'Inhabilitados'"
            flat
            :to="with_props ? {name: path, params: props.item, props: true} :{name: path, params: {id: props.item.id}}"
            icon
            color="green"
          >
            <v-icon>edit</v-icon>
          </v-btn>
          <v-btn v-if="title !== 'Inhabilitados' && title !== 'Docentes'" @click="with_props ? $emit('removeUser', props.item.cursa_id) : $emit('removeUser', props.item.id)" flat icon color="red">
            <v-icon>delete</v-icon>
          </v-btn>
          <v-btn
            flat
            v-if="title === 'Inhabilitados'"
            @click="$emit('nowEnableBook', props.item.id)"
            icon
            color="green"
          >
            <v-icon>check_circle_outline</v-icon>
          </v-btn>
        </td>
        <td v-else>
          <v-btn @click="$emit('updateTeacher', props.item.id)" flat icon color="green">
            <v-icon>person_add</v-icon>
          </v-btn>
          <v-btn
            flat
            @click="$emit('nowDisabledBook', props.item.id)"
            icon
            color="blue"
          >
            <v-icon>block</v-icon>
          </v-btn>
          <v-btn @click="$emit('nowCloseBook', props.item.id)" flat icon color="red">
            <v-icon>cancel_presentation</v-icon>
          </v-btn>
        </td>
      </template>
      <v-alert
        slot="no-results"
        :value="true"
        color="error"
        icon="warning"
      >Your search for "{{ search }}" found no results.</v-alert>
    </v-data-table>
  </v-card>
</template>

<script>
export default {
  name: "user-list",
  props: {
    item_list: {
      type: Array,
      required: true
    },
    title: {
      type: String,
      required: false
    },
    headers: {
      type: Array,
      required: true
    },
    search: {
      type: String,
      default: null,
      required: false
    },
    path: {
      type: String,
      required: true
    },
    action: {
      type: String,
      required: false
    },
    card_action: {
      type: Boolean,
      required: false,
    },
    with_props: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data() {
    return {
      query: this.search,
      opened: false
    };
  }
};
</script>

