        :search="search"
        :headers="headers"
        v-on:removeUser="removeLevel($event)"
        path="update-level"
      ></users-list>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import UsersList from "@/components/List/UsersList.vue";
export default {
  components: {
    UsersList
  },
  data() {
    return {
      search: ""
    };
  },
  computed: {
    ...mapGetters({
      levels: "level/levels",
      headers: "level/headers"
    })
  },
  methods: {
    ...mapActions({
      fetchLevels: "level/fetchLevels"
    }),
    removeLevel(id) {
      console.log("Eliminado: ", id);
    }
  },
  mounted() {
    this.fetchLevels();
  }
};
</script>

