/* Axios */
import { axios } from "./../../main";

const branch_office = {
    namespaced: true,

    state: {
        branch_offices: [],
        headers: [],
        query: {
            search: ''
        }
    },

    getters: {
        branch_offices: (state) => state.branch_offices,
        headers: (state) => state.headers
    },

    actions: {
        updateBranchOffice: ({ commit }, branch_office) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.put('sucursal/', branch_office)
                    .then((data) => {
                        console.log(data);
                        if (data.data.code === 200) {
                            commit('receivedBranchOffices', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data)
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err)
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            })
        },
        fetchBranchOffices: ({ commit }) => {
            return new Promise((resolve, reject) => {
                axios.get('sucursal/')
                    .then((data) => {
                        console.log(data);
                        if (data.data.code === 200) {
                            commit('receivedBranchOffices', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data)
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err)
                    })
            })
        }
    },

    mutations: {
        receivedBranchOffices: (state, apiResponse) => {
            state.branch_offices = apiResponse.data.content.data.sucursales;
            let headers = [];
            for (const key in state.branch_offices[0]) {
                let ingresa = false;
                let admin = {};
                switch (key) {
                    case 'id':
                        admin.text = 'Identificador';
                        ingresa = true;
                        break;
                    case 'nombre':
                        ingresa = true;
                        admin.text = 'Nombre';
                        break;
                    case 'dir':
                        ingresa = true;
                        admin.text = 'Direccin';
                        break;
                    default:
                        break;
                }
                admin.value = key;
                admin.align = 'center';
                if (ingresa) {
                    headers.push(admin);
                }
            }
            headers.push({
                text: 'Acciones',
                value: 'botones',
                align: 'center',
                sortable: false,
            })
            state.headers = headers;
        },
    }
};


export default branch_office;