<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
      <v-spacer></v-spacer>
      <!-- <v-icon>person</v-icon> -->
      <v-toolbar-title>Actualizar datos del Docente</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
      <update-user
        :form="form"
        :cambios="cambios"
        :update="true"
        v-on:updateRegister="updateRegister()"
      ></update-user>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import UpdateUser from "@/components/Form/UpdateUser.vue";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      names: "",
      lastname: "",
      email: "",
      address: "",
      phone: "",
      mobile: "",
      show: false,
      password: "",
      confirm: "",
      genre: "",
      user: null
    };
  },
  computed: {
    ...mapGetters({
      teachers: "teacher/teachers"
    }),
    form() {
      return {
        nombres: this.names,
        apellidos: this.lastname,
        correo: this.email,
        dir: this.address,
        telf: this.phone,
        cel: this.mobile,
        sexo: this.genre,
        password: this.password
      };
    }
  },
  methods: {
    ...mapActions({
      fetchTeachers: "teacher/fetchTeachers",
      updateTeacher: "teacher/updateTeacher"
    }),

    cambios() {
      let data = {};
      if (Object.keys(this.form).length > 0) {
        Object.keys(this.form).forEach(key => {
          if (!!this.form[key] && this.form[key] !== this.user[key]) {
            data[key] = this.form[key];
          }
        });
      }
      return data;
    },

    updateRegister() {
      console.log("Aqui va el Axios");
      this.updateTeacher({
        id: this.$route.params.id,
        datos: this.cambios()
      }).then(() => {
        //   this.resetForm();
        console.log("Se hizo correctamente");
      });
    },

    validateBeforeSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) {
            // fallan las validaciones
            // console.log('Algo anda mal', result);
          } else {
            console.log(this.cambios());

            if (Object.keys(this.cambios).length > 0) {
              this.updateTeacher({
                id: this.$route.params.id,
                datos: this.cambios
              }).then(() => {
                //   this.resetForm();
              });
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    this.fetchTeachers().then(() => {
      const id = Number(this.$route.params.id);
      const user = this.teachers.find(teacher => teacher.id === id);
      this.user = user;

      this.names = user.nombres;
      this.lastname = user.apellidos;
      this.email = user.correo;
      this.address = user.dir;
      this.phone = user.telf;
      this.mobile = user.cel;
      this.genre = user.sexo;
    });
  }
};
</script>

