/* Axios */
import { axios } from "../../main";

const book = {
    namespaced: true,

    state: {
        list_enabled: [],
        list_disabled: [],
        books: [],
        headers: [],
        query: {
            search: ''
        }
    },

    getters: {
        list_enabled: (state) => state.list_enabled.sort((curso1, curso2) => {
            if (Number(curso1.libro.nro.split(' ')[2]) > (curso2.libro.nro.split(' ')[2])) {
                return 1;
            } else if (Number(curso1.libro.nro.split(' ')[2]) < (curso2.libro.nro.split(' ')[2])){
                return -1;
            }
            return 0;
        }),
        list_disabled: (state) => state.list_disabled,
        books: (state) => state.books,
        headers: (state) => state.headers,
    },

    actions: {
        openBook: ({ commit }, book) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.post('curso/', { datos: book })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        updateCourse: ({ commit }, book) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                console.log(book);

                axios.put('curso/', book)
                    .then((data) => {
                        console.log(data);
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        enableBook: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.post('curso/habilitar', { id })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        disableBook: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.post('curso/inhabilitar', { id })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        closeBook: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.post('curso/cerrar', { id })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        deleteBook: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.delete('curso/', { data: { id } })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        fetchCourses: ({ commit }) => {

            return new Promise((resolve, reject) => {
                axios.get('curso/')
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedCourses', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        },

        fetchBooks: ({ commit }) => {
            return new Promise((resolve, reject) => {
                axios.get('libro/')
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedBook', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        },

        getHistory: ({ commit }) => {
            return new Promise((resolve, reject) => {
                axios.get('curso/historial')
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedBook', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        },
    },
    mutations: {
        receivedBook: (state, apiResponse) => {
            state.books = apiResponse.data.content.data.libros;
            state.books.forEach(libro => {
                libro.nro = 'Libro ' + libro.nro
            });
        },
        receivedCourses: (state, apiResponse) => {
            state.list_enabled = apiResponse.data.content.data.cursos.habilitados;
            state.list_enabled.filter((curso) => {
                curso.libro.nro = "Libro " + " " + curso.libro.nro;
            });
            state.list_disabled = apiResponse.data.content.data.cursos.inhabilitados;
            state.list_disabled.filter((curso) => {
                curso.libro.nro = "Libro " + " " + curso.libro.nro;
            });
            let headers = [];
            for (const key in state.list_enabled[0]) {
                let position = -1;
                let ingresa = false;
                let enabled = {};
                switch (key) {
                    case 'libro':
                        ingresa = true;
                        position = 0;
                        enabled.text = 'Libro';
                        break;
                    case 'admin':
                        ingresa = true;
                        position = 1;
                        enabled.text = 'Docente Encargado';
                        break;
                    case 'faper':
                        ingresa = true;
                        position = 2;
                        enabled.text = 'Fecha de Apertura';
                        break;
                    case 'fcierre':
                        ingresa = false;
                        position = 3;
                        enabled.text = 'Fecha de Cierre';
                        break;
                }
                enabled.value = key;
                enabled.align = 'center';
                if (ingresa) {
                    headers[position] = enabled;
                }
            }
            headers.push({
                text: 'Acciones',
                value: 'botones',
                align: 'center',
                sortable: false,
            })
            state.headers = headers;

            if (state.books) {
                let lista = state.books.filter(libro => {
                    if (!state.list_enabled.find(enable => enable.libro.id === libro.id) && !state.list_disabled.find(disabled => disabled.libro.id === libro.id)) {
                        return libro;
                    }
                });

                state.books = lista;

            }

        },
    }
};

export default book;