/* Axios */
import { axios } from "../../main";

const level = {
    namespaced: true,

    state: {
        levels: [],
        headers: [],
        query: {
            search: ''
        }
    },

    getters: {
        levels: (state) => state.levels,
        headers: (state) => state.headers,
    },

    actions: {
        registerLevel: ({ commit }, level) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.post('nivel/', { datos: level })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedLevels', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        updateLevel: ({ commit }, level) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.put('nivel/', level)
                    .then((data) => {
                        console.log(data);
                        if (data.data.code === 200) {
                            commit('receivedLevels', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        deleteLevel: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.delete('nivel/', { data: { id } })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedLevels', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        fetchLevels: ({ commit }) => {

            return new Promise((resolve, reject) => {
                axios.get('nivel/')
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedLevels', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        }
    },
    mutations: {
        receivedLevels: (state, apiResponse) => {
            state.levels = apiResponse.data.content.data.niveles;
            let headers = [];
            for (const key in state.levels[0]) {
                let ingresa = false;
                let level = {};
                switch (key) {
                    case 'nombre':
                        ingresa = true;
                        level.text = 'Nombre';
                        break;
                    case 'notamin':
                        ingresa = true;
                        level.text = 'Nota Mnima';
                        break;
                }
                level.value = key;
                level.align = 'center';
                if (ingresa) {
                    headers.push(level);
                }
            }
            headers.push({
                text: 'Acciones',
                value: 'botones',
                sortable: false,
            })
            state.headers = headers;

        },
    }
};

export default level;