<template>
  <v-container grid-list-md text-xs-center>
    <v-layout justify-center>
      <v-flex xs12>
        <v-card>
          <v-card-title>
            <h1>Historial</h1>
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Buscar..."
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            v-if="courses_list"
            :headers="headers"
            :items="lista_history"
            :search="search"
          >
            <template slot="items" slot-scope="props">
              <td v-for="(key, i) in headers" :key="i">
                <span v-if="key.text !== 'Acciones'">{{ props.item[key.value] }}</span>
                <span v-else>
                  <v-btn
                    flat
                    :to="{name: 'course', params: {id: props.item.id}}"
                    icon
                    color="green"
                  >
                    <v-icon>remove_red_eye</v-icon>
                  </v-btn>
                  <v-btn tag="a" flat target="_blank" :href="'http://sansebastianb.com/fab_api/reporte/curso/' + props.item.id" icon color="red">
                    <v-icon>print</v-icon>
                  </v-btn>
                </span>
              </td>
            </template>
            <v-alert
              slot="no-results"
              :value="true"
              color="error"
              icon="warning"
            >Your search for "{{ search }}" found no results.</v-alert>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      search: "",
      courses_list: null
    };
  },
  computed: {
    lista_history() {
      let list = [];
      if (this.courses_list != null && this.courses_list != []) {
        this.courses_list.forEach(curso => {
          list.push({
            libro: curso.libro.nro,
            admin: curso.admin
              ? `${curso.admin.nombres} ${curso.admin.apellidos}`
              : "Sin Asignar",
            faper: curso.faper,
            fcierre: curso.fcierre,
            id: curso.id,
            id_libro: curso.libro.id,
            id_admin: curso.admin ? curso.admin.id : -1
          });
        });
      }
      return list;
    },
    headers() {
      let headers = [];
      if (this.courses_list != null && this.courses_list != []) {
        Object.keys(this.courses_list[0]).forEach(key => {
          let header = {};
          let position = -1;
          let entra = false;
          switch (key) {
            case "libro":
              header.text = "Libro";
              position = 0;
              entra = true;
              break;
            case "admin":
              header.text = "Docente";
              position = 1;
              entra = true;
              break;
            case "faper":
              header.text = "Fecha de Apertura";
              position = 2;
              entra = true;
              break;
            case "fcierre":
              header.text = "Fecha de Cierre";
              position = 3;
              entra = true;
              break;
            default:
              break;
          }
          header.value = key;
          header.align = "center";
          if (entra) {
            headers[position] = header;
          }
        });
        headers.push({
          text: "Acciones",
          value: "actions",
          align: "center",
          sortable: false
        });
      }
      return headers;
    }
  },
  methods: {
    ...mapActions({
      getHistory: "book/getHistory"
    })
  },
  mounted() {
    this.getHistory().then(data => {
      this.courses_list = data.data.content.data.cursos;
    });
  }
};
</script>

