<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
       <v-toolbar class="primary--text" color="transparent" center card dark flat>
      <v-spacer></v-spacer>
      <!-- <v-icon>person</v-icon> -->
      <v-toolbar-title>Inscripcin de Estudiante</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
      <update-user :form="form" :select_items="list_enabled" :update="false" v-on:createRegister="createRegister()"></update-user>
    </v-flex>
  </v-layout>
</template>

<script>
import UpdateUser from "@/components/Form/UpdateUser.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      names: "",
      lastname: "",
      ci: "",
      mobile: "",
      email: "",
      genre: "",
      course_id: null
    };
  },
  computed: {
    ...mapGetters({
        list_enabled: "book/list_enabled",
    }),
    form() {
      return {
        nombres: this.names,
        apellidos: this.lastname,
        correo: this.email,
        ci: this.ci,
        cel: this.mobile,
        sexo: this.genre,
        curso_id: this.course_id
      };
    }
  },
  methods: {
    ...mapActions({
      registerStudent: "student/registerStudent",
      fetchBooks: "book/fetchBooks",
      fetchCourses: "book/fetchCourses",
    }),
    resetForm() {
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
      this.$validator.reset();
    },

    createRegister() {
        this.registerStudent(this.form).then(() => {
          this.$router.push({name: 'list-students'})
        })
    }
  },
  mounted() {
    console.log(this.$route.params);
    this.fetchBooks().then(() => {
      this.fetchCourses();
    });
  }
};
</script>

