<template>
  <v-container grid-list-md text-xs-center>
    <v-layout row wrap justify-center>
      <v-flex xs12>
        <v-toolbar class="primary--text" color="transparent" center card dark flat>
          <v-spacer></v-spacer>
          <!-- <v-icon>person</v-icon> -->
          <v-toolbar-title>{{ $t('search_student.title') }}</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
      </v-flex>
      <v-flex xs12 sm6 md6 lg5>
        <form ref="form" autocomplete="false" @submit.prevent="buscar">
          <v-card-text>
            <v-flex xs12 class="pa-0 ma-0">
              <v-text-field
                autocomplete="off"
                autofocus
                v-model="ci"
                ref="search"
                name="search"
                append-icon="search"
                @click:append-outer="buscar"
                :label="$t('search_student.field')"
                solo
              ></v-text-field>
            </v-flex>
          </v-card-text>
          <!-- <v-card-actions class="pa-0 ma-0">
        <v-spacer></v-spacer>
        <v-btn type="submit" color="primary" flat>{{ $t('new.submit') }}</v-btn>
          </v-card-actions>-->
        </form>
      </v-flex>
      <v-flex v-if="cursos" xs12>
        <v-card>
          <v-card-title>
            <v-spacer></v-spacer>
            <h2>{{ `${ nombres.toUpperCase() } ${ apellidos.toUpperCase() }`}}</h2>
            <v-spacer></v-spacer>
          </v-card-title>
          <v-data-table hide-actions :headers="headers" :items="cursos">
            <template slot="items" slot-scope="props">
              <td v-for="(key, i) in headers" class="text-xs-center" :key="i">
                <span>{{ props.item[key.value] != null ? props.item[key.value] : '-' }}</span>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      ci: "",
      cursos: null,
      nombres: null,
      apellidos: null
    };
  },
  computed: {
    headers() {
      let headers = [];
      if (this.cursos.length > 0) {
        Object.keys(this.cursos[0]).forEach(student => {
          // console.log(student);
          let position = -1;
          let item = {};
          switch (student) {
            case "libro":
              item.text = "Libro";
              position = 0;
              break;
            case "docente":
              item.text = "Docente";
              position = 1;
              break;
            case "apertura":
              item.text = "F. de Apertura";
              position = 2;
              break;
            case "cierre":
              item.text = "F. de Cierre";
              position = 3;
              break;
            case "atencion":
              item.text = "Nota de Atencin";
              position = 4;
              break;
            case "habla":
              item.text = "Nota de Habla";
              position = 5;
              break;
            case "participacion":
              item.text = "Nota de Participacin";
              position = 6;
              break;
            case "examen":
              item.text = "Nota de Exmen";
              position = 7;
              break;
            default:
              break;
          }
          item.align = "center";
          item.value = student;

          headers[position] = item;
        });
      }
      return headers;
    }
  },
  methods: {
    ...mapActions({
      consultar: "student/consultarNota"
    }),

    buscar() {
      if (this.ci !== "") {
        this.consultar(this.ci).then(data => {
          this.cursos = data.data.content.estudiante.cursos;
          this.nombres = data.data.content.estudiante.nombres;
          this.apellidos = data.data.content.estudiante.apellidos;
        });
        this.ci = "";
      }
    }
  }
};
</script>

