<template>
  <v-layout row wrap justify-center>
    <v-flex xs12>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
        <v-spacer></v-spacer>
        <!-- <v-icon>person</v-icon> -->
        <v-toolbar-title>{{ $t('search_student.title') }}</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-flex>
    <v-flex xs12 sm6 md6 lg5>
      <form ref="form" autocomplete="false" @submit.prevent="buscar">
        <v-card-text>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="ci"
              ref="search"
              name="search"
              append-icon="person"
              :label="$t('search_student.field')"
              solo
            ></v-text-field>
          </v-flex>
        </v-card-text>
        <!-- <v-card-actions class="pa-0 ma-0">
        <v-spacer></v-spacer>
        <v-btn type="submit" color="primary" flat>{{ $t('new.submit') }}</v-btn>
        </v-card-actions>-->
      </form>
    </v-flex>
    <v-flex v-if="student" xs12>
      <v-card>
        <v-card-title>
          <v-spacer></v-spacer>
          <h2>{{ `${ student.nombres } ${ student.apellidos }`}}</h2>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-data-table hide-actions :headers="head" :items="items">
          <template slot="items" slot-scope="props">
            <td v-for="(key, i) in head" :key="i">
              <span>{{ props.item[key.value] }}</span>
            </td>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      ci: null,
      student: null,
      search: "",
    };
  },

  computed: {
    head() {
      let headers = [];
      Object.keys(this.student.notas).forEach(student => {
        console.log(student);

        let item = {};
        switch (student) {
          case "atencion":
            item.text = "Atencin";
            break;
          case "habla":
            item.text = "Habla";
            break;
          case "participacion":
            item.text = "Participacin";
            break;
          case "examen":
            item.text = "Exmen";
            break;
          default:
            break;
        }
        item.align = "center";
        item.value = student;

        headers.push(item);
      });
      return headers;
    },
    items() {
      let array = [];
      array.push(this.student.notas);
      return array;
    }
  },

  methods: {
    ...mapActions({
      searchStudent: "student/searchStudent"
    }),
    buscar() {
      this.searchStudent({ ci: this.ci }).then(data => {
        if (data.data.content.data.estudiante === null) {
          this.$router.push({ name: "new-student" });
        } else {
          if (data.data.content.data.estudiante.estado === "Cursando") {
            this.student = data.data.content.data.estudiante;
          } else if (
            data.data.content.data.estudiante.estado === "Reprobado" ||
            data.data.content.data.estudiante.estado === "Aprobado"
          ) {
            this.$router.push({
              name: "enroll-student",
              params: data.data.content.data.estudiante,
              props: true
            });
          }

          console.log(data);
        }
      });
    }
  }
};
</script>

