<template>
  <v-layout justify-center>
    <v-flex xs12 sm10 md8 lg7>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
      <v-spacer></v-spacer>
      <!-- <v-icon>person</v-icon> -->
      <v-toolbar-title>Actualizar datos de la Sucursal</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

      <form ref="form" autocomplete="false" @submit.prevent="validateBeforeSubmit">
        <v-card-text>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="name"
              ref="nombre"
              name="nombre"
              v-validate="'required|min:3'"
              :error-messages="errors.first('names')"
              data-vv-name="names"
              append-icon="person"
              :label="$t('new.names')"
              solo
            ></v-text-field>
          </v-flex>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="address"
              ref="dir"
              name="dir"
              v-validate="'required'"
              :error-messages="errors.first('address')"
              data-vv-name="address"
              append-icon="place"
              :label="$t('new.address')"
              solo
            ></v-text-field>
          </v-flex>
        </v-card-text>
        <v-card-actions class="pa-0 ma-0">
          <v-spacer></v-spacer>
          <v-btn type="submit" color="primary" flat>{{ $t('new.submit') }}</v-btn>
        </v-card-actions>
      </form>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      name: "",
      address: "",
      branch_office: null
    };
  },
  computed: {
    ...mapGetters({
      branch_offices: "branch_office/branch_offices"
    }),
    form() {
      return {
        nombre: this.name,
        dir: this.address
      };
    },
    cambios() {
      let data = {};
    //   console.log(Object.keys(this.form));
      if (Object.keys(this.form).length > 0) {
        Object.keys(this.form).forEach(key => {
          if (
            !!this.$refs[key].value &&
            this.$refs[key].value !== this.branch_office[key]
          ) {
            data[key] = this.$refs[key].value;
          }
        });
      }
      return data;
    }
  },
  methods: {
    ...mapActions({
      fetchBranchOffices: "branch_office/fetchBranchOffices",
      updateAdministrator: "branch_office/updateBranchOffice"
    }),

    validateBeforeSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) {
            // fallan las validaciones
            // console.log('Algo anda mal', result);
          } else {
            if (Object.keys(this.cambios).length > 0) {
                this.updateAdministrator({
                  id: this.$route.params.id,
                  datos: this.cambios
                }).then(() => {
                  //   this.resetForm();}
                  this.$router.push({name: 'list-branch-offices'})
                });
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    this.fetchBranchOffices().then(() => {
      const id = Number(this.$route.params.id);
      const branch_office = this.branch_offices.find(
        branch => branch.id === id
      );
      this.branch_office = branch_office;

      this.name = branch_office.nombre;
      this.address = branch_office.dir;
    });
  }
};
</script>

