<template>
  <v-container grid-list-md text-xs-center>
    <v-layout justify-center>
      <v-flex xs12 sm6 md6 lg5>
        <v-img :src="require('@/assets/logo-eifab3.png')" width="100%"></v-img>
        <v-toolbar class="primary--text" color="transparent" center card dark flat>
          <v-spacer></v-spacer>
          <!-- <v-icon>person</v-icon> -->
          <v-toolbar-title>{{ $t('login.title') }}</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <form ref="form" autocomplete="false" @submit.prevent="validateBeforeSubmit">
          <v-card-text>
            <!-- <v-text-field autocomplete="off" autofocus v-model="email" ref="email" name="email" data-vv-name="email" label="User"></v-text-field> -->
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="email"
              ref="correo"
              name="email"
              v-validate="'required|email'"
              :error-messages="errors.first('email')"
              data-vv-name="email"
              append-icon="person"
              :label="$t('login.email')"
              solo
            ></v-text-field>
            <v-text-field
              v-model="password"
              ref="password"
              name="password"
              :append-icon="show1 ? 'visibility_off' : 'visibility'"
              :type="show1 ? 'text' : 'password'"
              v-validate="'required|min:3'"
              data-vv-name="password"
              :error-messages="errors.first('password')"
              :label="$t('login.password')"
              @click:append="show1 = !show1"
              solo
            ></v-text-field>
            <!-- <v-alert :value="error" color="error" icon="warning">{{ $t('login.error') }}</v-alert> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-slide-x-reverse-transition>
              <v-tooltip left>
                <v-btn slot="activator" icon class="my-0" @click="resetForm">
                  <v-icon color="primary">refresh</v-icon>
                </v-btn>
                <span>Refresh form</span>
              </v-tooltip>
            </v-slide-x-reverse-transition>
            <v-btn type="submit" color="primary" flat>{{ $t('login.title') }}</v-btn>
          </v-card-actions>
        </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapFields } from "vee-validate";
import { mapActions } from "vuex";
// import axios from "axios";
export default {
  name: "login",
  data() {
    return {
      email: "",
      password: "",
      error: null,
      show1: false,
      formHasError: false
    };
  },
  computed: {
    form() {
      return {
        correo: this.email,
        password: this.password
      };
    },
    ...mapFields({
      emailFlags: "email",
      passwordFlags: "password"
    })
  },
  methods: {
    ...mapActions({
      login: "auth/login"
    }),

    resetForm() {
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
      this.$validator.reset();
    },

    validateBeforeSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) {
            // fallan las validaciones
          } else {
            this.login(this.form)
              .then(data => {
                if (data.data.content.data.user.tipo === "root") {
                  this.$router.push("/branch_office/list");
                } else if (
                  data.data.content.data.user.tipo === "administrativo"
                ) {
                  this.$router.push("/student/qualify");
                } else if (data.data.content.data.user.tipo === "docente") {
                  this.$router.push("/qualify");
                }
              })
              .catch(err => {
                console.log(err);
                this.error = true;
              });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    localStorage.clear();
  }
};
</script>

