<template>
  <v-app class="background">
    <navigation></navigation>
    <!-- <div v-if="processing">
      <BlockUI :message="$t('messages.processing')">
        <div class="loader"></div>
      </BlockUI>
    </div>-->
    <v-content>
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
import { mapGetters } from "vuex";
import Navigation from "./components/Navigation";
export default {
  name: "App",
  components: {
    Navigation
  },
  data() {
    return {
      //
    };
  },
  computed: {
    ...mapGetters({
      processing: "global/processing",
      message: "global/message",
      correct: "global/success"
    })
  },

  watch: {
    // whenever question changes, this function will run
    processing: function(newQuestion, oldQuestion) {
      // this.answer = 'Waiting for you to stop typing...'
      // this.debouncedGetAnswer()
      oldQuestion;
      if (newQuestion) {
        console.log("Star Processing");
        this.loading();
      } else {
        this.$swal.hideLoading();
        if (this.correct) {
          this.success();
        } else {
          this.error()
        }
        console.log("Stop Processing");
      }
    }
  },
  methods: {
    loading() {
      this.$swal({
        title: this.$t("messages.processing"),
        allowEscapeKey: false,
        allowOutsideClick: false,
        onOpen: () => {
          this.$swal.showLoading();
        }
      });
      // .then(dismiss => {
      //   if (dismiss.dismiss === "timer") {
      //     console.log("closed by timer!!!!");
      //     this.$swal({
      //       title: "Finished!",
      //       type: "success",
      //       timer: 3000,
      //       showConfirmButton: false
      //     });
      //   }
      // });
    },
    success() {
      this.$swal({
        title: "Finished!",
        text: this.message,
        type: "success",
        timer: 1000,
        showConfirmButton: false
      });
    },

    error() {
      this.$swal({
        type: "error",
        title: "Oops...",
        text: this.message,
      });
    }
  }
};
</script>

<style scoped>
.loader:before,
.loader:after,
.loader {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out;
}
.loader {
  margin: auto;
  margin-bottom: 5em;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  -webkit-animation-delay: 0.16s;
  animation-delay: 0.16s;
}
.loader:before {
  left: -3.5em;
}
.loader:after {
  left: 3.5em;
  -webkit-animation-delay: 0.32s;
  animation-delay: 0.32s;
}
.loader:before,
.loader:after {
  content: "";
  position: absolute;
  top: 0;
}
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em #343e48;
  }
  40% {
    box-shadow: 0 2.5em 0 0 #343e48;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em #343e48;
  }
  40% {
    box-shadow: 0 2.5em 0 0 #343e48;
  }
}
</style>

