/* Axios */
import { axios } from "./../../main";

const teacher = {
    namespaced: true,

    state: {
        teachers: [],
        headers: [],
        select_teachers: [],
        query: {
            search: ''
        }
    },

    getters: {
        teachers: (state) => state.teachers,
        headers: (state) => state.headers,
        select_teachers: (state) => state.select_teachers,
    },

    actions: {
        registerTeacher: ({ commit }, teacher) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.post('docente/', { datos: teacher })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedTeachers', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        updateTeacher: ({ commit }, teacher) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.put('docente/', teacher)
                    .then((data) => {
                        console.log(data);
                        if (data.data.code === 200) {
                            commit('receivedTeachers', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        deleteTeacher: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.delete('docente/', { data: { id } })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedTeachers', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        fetchCourses: ({ commit }) => {
            return new Promise((resolve, reject) => {
                axios.get('docente/cursos')
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedTeachers', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        },

        fetchTeachers: ({ commit }) => {

            return new Promise((resolve, reject) => {
                axios.get('docente/')
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedTeachers', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        }
    },
    mutations: {
        receivedTeachers: (state, apiResponse) => {
            state.teachers = apiResponse.data.content.data.docentes;
            let headers = [];
            for (const key in state.teachers[0]) {
                let ingresa = false;
                let teacher = {};
                switch (key) {
                    case 'correo':
                        ingresa = true;
                        teacher.text = 'Correo';
                        break;
                    case 'nombres':
                        ingresa = true;
                        teacher.text = 'Nombres';
                        break;
                    case 'apellidos':
                        ingresa = true;
                        teacher.text = 'Apellidos';
                        break;
                    case 'dir':
                        ingresa = false;
                        teacher.text = 'Direccin';
                        teacher.sortable = false;
                        break;
                    case 'telf':
                        ingresa = true;
                        teacher.text = 'Telfono';
                        teacher.sortable = false;
                        break;
                    case 'cel':
                        ingresa = true;
                        teacher.text = 'Celular'
                        teacher.sortable = false;
                        break;
                    case 'sexo':
                        ingresa = false;
                        teacher.text = 'Gnero';
                        break;
                    default:
                        break;
                }
                teacher.value = key;
                teacher.align = 'center';
                if (ingresa) {
                    headers.push(teacher);
                }
            }
            headers.push({
                text: 'Acciones',
                value: 'botones',
                sortable: false,
            })
            state.headers = headers;

            let select_teachers = [];

            apiResponse.data.content.data.docentes.forEach(teacher => {
                select_teachers.push({
                    id: teacher.id,
                    name: teacher.nombres + ' ' + teacher.apellidos
                })
            });

            state.select_teachers = select_teachers;

        },
    }
};

export default teacher;