<template>
  <v-container grid-list-md text-xs-center>
    <v-layout justify-center>
      <v-flex xs12 sm6 md6 lg5>
        <v-toolbar class="primary--text" color="transparent" center card dark flat>
          <v-spacer></v-spacer>
          <!-- <v-icon>person</v-icon> -->
          <v-toolbar-title>{{ $t('profile.title') }}</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>

        <form ref="form" autocomplete="false" @submit.prevent="validateBeforeSubmit">
          <v-card-text>
            <!-- <v-text-field autocomplete="off" autofocus v-model="email" ref="email" name="email" data-vv-name="email" label="User"></v-text-field> -->
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="correo"
              ref="correo"
              name="correo"
              v-validate="'email|min:3'"
              :error-messages="errors.first('email')"
              data-vv-name="correo"
              :label="$t('profile.email')"
              solo
            ></v-text-field>
            <v-text-field
              v-model="nombres"
              name="nombres"
              v-validate="'min:3'"
              ref="nombres"
              :label="$t('profile.names')"
              solo
            ></v-text-field>
            <v-text-field v-model="apellidos" ref="apellidos" :label="$t('profile.lastname')" solo></v-text-field>
            <v-text-field
              v-model="password"
              ref="password"
              name="password"
              :append-icon="show ? 'visibility_off' : 'visibility'"
              :type="show ? 'text' : 'password'"
              v-validate="'min:3'"
              :error-messages="errors.first('password')"
              :label="$t('profile.password')"
              @click:append="show = !show"
              solo
            ></v-text-field>
            <v-text-field
              v-model="confirm"
              ref="confirm"
              name="password_confirmation"
              :append-icon="show ? 'visibility_off' : 'visibility'"
              :type="show ? 'text' : 'password'"
              v-validate="passwordFlags.changed ? 'required|confirmed:password' :'confirmed:password'"
              data-vv-as="password"
              :error-messages="errors.first('password_confirmation')"
              :label="$t('profile.confirm')"
              @click:append="show = !show"
              solo
            ></v-text-field>
            <!-- <v-alert :value="error" color="error" icon="warning">{{ $t('login.error') }}</v-alert> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-slide-x-reverse-transition>
              <!-- <v-tooltip left>
                <v-btn slot="activator" icon class="my-0" @click="resetForm">
                  <v-icon color="primary">refresh</v-icon>
                </v-btn>
                <span>Refresh form</span>
              </v-tooltip>-->
            </v-slide-x-reverse-transition>
            <v-btn type="submit" color="primary" flat>{{ $t('profile.title') }}</v-btn>
          </v-card-actions>
        </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapFields } from "vee-validate";
import { mapActions } from "vuex";
export default {
  data() {
    return {
      show: false,
      correo: "",
      nombres: "",
      apellidos: "",
      password: "",
      confirm: "",
      user: null
    };
  },
  computed: {
    ...mapFields({
      nombresFlags: "nombres",
      apellidosFlags: "apellidos",
      correoFlags: "correo",
      passwordFlags: "password"
    }),
    form() {
      return {
        correo: this.correo,
        nombres: this.nombres,
        apellidos: this.apellidos,
        password: this.password
      };
    }
  },
  mounted() {
    this.user = JSON.parse(localStorage.getItem("_user"));
    this.correo = this.user.correo;
    this.nombres = this.user.nombres;
    this.apellidos = this.user.apellidos;
  },

  methods: {
    ...mapActions({
      updateProfile: "auth/updateProfile"
    }),
    validateBeforeSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) {
            // fallan las validaciones
          } else {
            let data = {};
            Object.keys(this.form).forEach(f => {
              console.log(this.$refs[f].value);
              if (
                this.$refs[f].value !== "" &&
                this.$refs[f].value !== this.user[f]
              ) {
                data[f] = this.$refs[f].value;
              }
            });

            if (data !== {}) {
              this.updateProfile(data)
                .then(() => {
                  this.$router.push("/student/list");
                })
                .catch(err => {
                  console.log(err);
                  // this.error = true;
                });
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

