<template>
  <v-layout justify-center>
    <v-flex xs12 sm12 md12 lg12>
      <v-card flat class="secondary">
        <v-card-title>
          <h1>Sucursales</h1>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          color="transparent"
          :headers="headers"
          :items="branch_offices"
          :search="search"
        >
          <template color="transparent" slot="items" slot-scope="props">
            <td
              v-for="(key, index) in headers"
              v-if="key.text !== 'Acciones'"
              :key="index"
            >{{ props.item[key.value] }}</td>
            <td>
              <v-btn
                class="mr-0"
                flat
                :to="{name: 'update-branch-office', params: {id: props.item.id}}"
                icon
                color="green"
              >
                <v-icon>edit</v-icon>
              </v-btn>
              <v-btn @click="removeAdministrator(props.item.id)" flat icon color="red">
                <v-icon>delete</v-icon>
              </v-btn>
            </td>
          </template>
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >Your search for "{{ search }}" found no results.</v-alert>
        </v-data-table>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
    data() {
        return {
            search: ''
        }
    },
  computed: {
    ...mapGetters({
      branch_offices: "branch_office/branch_offices",
      headers: "branch_office/headers"
    })
  },

  methods: {
    ...mapActions({
      fetchBranchOffices: "branch_office/fetchBranchOffices"
    })
  },
  
  mounted() {
    this.fetchBranchOffices();
  }
};
</script>

