/* Axios */
import { axios } from "./../../main";

const administrator = {
    namespaced: true,

    state: {
        administrators: [],
        headers: [],
        query: {
            search: ''
        }
    },

    getters: {
        administrators: (state) => state.administrators,
        headers: (state) => state.headers,
    },

    actions: {
        registerAdministrator: ({ commit }, administrator) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.post('admin/', { datos: administrator })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedAdministrators', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        updateAdministrator: ({ commit }, administrator) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.put('admin/', administrator)
                    .then((data) => {
                        console.log(data);
                        if (data.data.code === 200) {
                            commit('receivedAdministrators', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        deleteAdministrator: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                
                axios.delete('admin/', { data: { id } })
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedAdministrators', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);
                        
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        fetchAdministrators: ({ commit }) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.get('admin/')
                    .then((data) => {
                        if (data.data.code === 200) {
                            commit('receivedAdministrators', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            })
        }
    },

    mutations: {
        receivedAdministrators: (state, apiResponse) => {
            state.administrators = apiResponse.data.content.data.administrativos;
            let headers = [];
            for (const key in state.administrators[0]) {
                let ingresa = false;
                let admin = {};
                switch (key) {
                    case 'sucursal_id':
                        admin.text = 'Sucursal';
                        ingresa = true;
                        break;
                    case 'correo':
                        ingresa = true;
                        admin.text = 'Correo';
                        break;
                    case 'nombres':
                        ingresa = true;
                        admin.text = 'Nombres';
                        break;
                    case 'apellidos':
                        ingresa = true;
                        admin.text = 'Apellidos';
                        break;
                    case 'dir':
                        ingresa = false;
                        admin.text = 'Direccin';
                        admin.sortable = false;
                        break;
                    case 'telf':
                        ingresa = true;
                        admin.text = 'Telfono';
                        admin.sortable = false;
                        break;
                    case 'cel':
                        ingresa = true;
                        admin.text = 'Celular'
                        admin.sortable = false;
                        break;
                    case 'sexo':
                        ingresa = false;
                        admin.text = 'Gnero';
                        break;
                    default:
                        break;
                }
                admin.value = key;
                admin.align = 'center';
                if (ingresa) {
                    headers.push(admin);
                }
            }
            headers.push({
                text: 'Acciones',
                value: 'botones',
                sortable: false,
            })
            state.headers = headers;

        },
    }
};

export default administrator;