<template>
  <v-layout row wrap justify-center>
    <v-flex xs12>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
        <v-spacer></v-spacer>
        <!-- <v-icon>person</v-icon> -->
        <v-toolbar-title>Calificar curso</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-autocomplete
        :items="select_course"
        item-text="book_and_teacher"
        v-model="course_id"
        item-value="id"
        clearable
        label="Seleccione un Docente"
        solo
        flat
      ></v-autocomplete>
    </v-flex>
    <v-flex v-if="course_id && course" xs12>
      <v-card>
        <v-card-title>Nutrition
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="course.estudiantes" :search="search">
          <template slot="items" slot-scope="props">
            <td
              v-for="(key, i) in headers"
              :key="i"
              v-if="key.text !== 'Acciones'"
              class="text-xs-right"
            >
              <span
                v-if="key.value !== 'natencion' && key.value !== 'nhabla' && key.value !== 'npart' && key.value !== 'nexamen'"
              >{{ props.item[key.value] }}</span>
              <v-text-field
                v-if="key.value === 'natencion'"
                class="ma-0 pa-0 mt-4"
                reverse
                v-model="list_student[props.item.cursa_id].natencion"
                :label="props.item[key.value] ? props.item[key.value] + '': '0'"
                flat
              ></v-text-field>
              <v-text-field
                v-if="key.value === 'nhabla'"
                class="ma-0 pa-0 mt-4"
                reverse
                v-model="list_student[props.item.cursa_id].nhabla"
                :label="props.item[key.value] ? props.item[key.value] + '': '0'"
                flat
              ></v-text-field>
              <v-text-field
                v-if="key.value === 'npart'"
                class="ma-0 pa-0 mt-4"
                reverse
                v-model="list_student[props.item.cursa_id].npart"
                :label="props.item[key.value] ? props.item[key.value] + '': '0'"
                flat
              ></v-text-field>
              <v-text-field
                v-if="key.value === 'nexamen'"
                class="ma-0 pa-0 mt-4"
                reverse
                v-model="list_student[props.item.cursa_id].nexamen"
                :label="props.item[key.value] ? props.item[key.value] + '': '0'"
                flat
              ></v-text-field>
            </td>
            <td v-else>
              <v-btn class="mr-0" flat @click="saveNotas(props.item.cursa_id)" color="green">
                <v-icon left>save</v-icon>Guardar
              </v-btn>
            </td>
          </template>
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >Your search for "{{ search }}" found no results.</v-alert>
        </v-data-table>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      course_id: null,
      search: "",
      atencion: "",
      habla: "",
      participacion: "",
      examen: "",
      course: null,
      list_student: []
    };
  },
  computed: {
    ...mapGetters({
      students: "student/students",
      list_enabled: "book/list_enabled",
      select_teachers: "teacher/select_teachers"
      //   headers: "student/headers"
    }),

    headers() {
      let headers = [];
      if (this.course != null) {
        if (this.course.estudiantes.length > 0) {
          Object.keys(this.course.estudiantes[0]).forEach(key => {
            let header = {};
            let position = -1;
            let entra = false;
            switch (key) {
              case "nombres":
                header.text = "Nombres";
                position = 0;
                entra = true;
                break;
              case "apellidos":
                header.text = "apellidos";
                position = 1;
                entra = true;
                break;
              case "ci":
                header.text = "ci";
                position = 2;
                entra = true;
                break;
              case "natencion":
                header.text = "Nota de Atencin";
                position = 3;
                entra = true;
                break;
              case "nhabla":
                header.text = "Nota de Habla";
                position = 4;
                entra = true;
                break;
              case "npart":
                header.text = "Nota de Participacin";
                position = 5;
                entra = true;
                break;
              case "nexamen":
                header.text = "Nota de Exmen";
                position = 6;
                entra = true;
                break;

              default:
                break;
            }
            header.value = key;
            header.align = "center";
            if (entra) {
              headers[position] = header;
            }
          });
          headers.push({
            text: "Acciones",
            value: "actions",
            align: "center",
            sortable: false
          });
        }
      }
      return headers;
    },
    form() {
      return {
        curso_id: this.course_id,
        cursa_id: null,
        datos: {
          natencion: this.atencion,
          nhabla: this.habla,
          npart: this.participacion,
          nexamen: this.examen
        }
      };
    },

    select_course() {
      let courses = [];
      this.list_enabled.forEach(course => {
        // if (course.admin) {
        courses.push({
          id: course.id,
          book_and_teacher: course.admin
            ? `${course.libro.nro} (${course.admin.nombres} ${
                course.admin.apellidos
              })`
            : `${course.libro.nro} (Sin Asignar)`
        });
        // }
      });
      return courses;
    }
  },

  watch: {
    course_id(newValue, oldValue) {
      this.course = null;
      if (newValue != undefined && newValue != null) {
        this.fetchNotas(newValue).then(data => {
          console.log("Notaaaas", data.data.content.data.curso);
          this.course = data.data.content.data.curso;
          if (this.course != null) {
            if (this.course.estudiantes.length > 0) {
              this.course.estudiantes.forEach(student => {
                this.list_student[student.cursa_id] = {
                  natencion: null,
                  nhabla: null,
                  npart: null,
                  nexamen: null
                };
              });
            }
          }
        });
      }
    }
  },
  methods: {
    ...mapActions({
      fetchTeachers: "teacher/fetchTeachers",
      fetchBooks: "book/fetchBooks",
      fetchCourses: "book/fetchCourses",
      updateNotas: "student/updateNotas",
      fetchStudents: "student/fetchStudents",
      deleteStudent: "student/deleteStudent",
      fetchNotas: "student/fetchNotas"
    }),

    cambios(user, datos) {
      let data = {};
      if (Object.keys(datos).length > 0) {
        Object.keys(datos).forEach(key => {
          if (!!datos[key] && datos[key] !== user[key]) {
            data[key] = datos[key];
          }
        });
      }
      return data;
    },

    saveNotas(id) {
      let form = {
        natencion: this.list_student[id].natencion,
        nhabla: this.list_student[id].nhabla,
        npart: this.list_student[id].npart,
        nexamen: this.list_student[id].nexamen
      };

      let user = this.course.estudiantes.find(student => student.cursa_id === id);

      let datos = this.cambios(user, form);
      // console.log(datos);

      if (Object.keys(datos).length > 0) {
        this.updateNotas({
          cursa_id: id,
          curso_id: this.course_id,
          datos
        }).then(() => {
          console.log("Notas guardadas del estudiante ", id);
        });
      }
    },

    removeStudent(id) {
      this.$swal({
        title: "Ests seguro?",
        text: "",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "S, hazlo!"
      }).then(result => {
        if (result.value) {
          this.deleteStudent(id).then(() => {
            this.fetchStudents();
          });
        }
      });
    }
  },
  mounted() {
    this.fetchTeachers();
    this.fetchBooks().then(() => {
      this.fetchCourses();
    });
    this.fetchStudents();
  }
};
</script>

