<template>
  <v-container grid-list-md text-xs-center>
    <v-layout row wrap justify-center>
      <v-flex xs12>
        <v-toolbar class="primary--text" color="transparent" center card dark flat>
          <v-spacer></v-spacer>
          <v-toolbar-title>Informacin del Curso</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
      </v-flex>
      <v-flex v-if="course_id && course" xs12>
        <v-card>
          <v-card-title>
              <h2>Libro {{course_id}}</h2>
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table :headers="headers" :items="course.estudiantes" :search="search">
            <template slot="items" slot-scope="props">
              <td
                v-for="(key, i) in headers"
                :key="i"
                v-if="key.text !== 'Acciones'"
                class="text-xs-right"
              >
                <span>{{ props.item[key.value] }}</span>
              </td>
              <td v-else>
                <v-btn tag="a" flat target="_blank" :href="'http://sansebastianb.com/fab_api/reporte/cursa/' + props.item.cursa_id" icon color="red">
                    <v-icon>print</v-icon>
                  </v-btn>
              </td>
            </template>
            <v-alert
              slot="no-results"
              :value="true"
              color="error"
              icon="warning"
            >Your search for "{{ search }}" found no results.</v-alert>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      course_id: null,
      course: null,
      search: ''
    };
  },
  computed: {
    ...mapGetters({
      //   students: "student/students",
      //   list_enabled: "book/list_enabled",
      //   select_teachers: "teacher/select_teachers"
      //   //   headers: "student/headers"
    }),

    headers() {
      let headers = [];
      if (this.course != null) {
        if (this.course.estudiantes.length > 0) {
          Object.keys(this.course.estudiantes[0]).forEach(key => {
            let header = {};
            let position = -1;
            let entra = false;
            switch (key) {
              case "nombres":
                header.text = "Nombres";
                position = 0;
                entra = true;
                break;
              case "apellidos":
                header.text = "apellidos";
                position = 1;
                entra = true;
                break;
              case "ci":
                header.text = "ci";
                position = 2;
                entra = true;
                break;
              case "natencion":
                header.text = "Nota de Atencin";
                position = 3;
                entra = true;
                break;
              case "nhabla":
                header.text = "Nota de Habla";
                position = 4;
                entra = true;
                break;
              case "npart":
                header.text = "Nota de Participacin";
                position = 5;
                entra = true;
                break;

              default:
                break;
            }
            header.value = key;
            header.align = "center";
            if (entra) {
              headers[position] = header;
            }
          });
          headers.push({
            text: "Acciones",
            value: "actions",
            align: "center",
            sortable: false
          });
        }
      }
      return headers;
    },
  },

  watch: {
    course_id(newValue, oldValue) {
      this.course = null;
      if (newValue != undefined && newValue != null) {
        this.fetchNotas(newValue).then(data => {
          console.log("Notaaaas", data.data.content.data.curso);
          this.course = data.data.content.data.curso;
        });
      }
    }
  },
  methods: {
    ...mapActions({
      fetchCourses: "teacher/fetchCourses",
      fetchNotas: "student/fetchNotas",
      updateNotas: "student/updateNotas"
    }),

    cambios(user) {
      let data = {};
      if (Object.keys(this.form.datos).length > 0) {
        Object.keys(this.form.datos).forEach(key => {
          if (!!this.form.datos[key] && this.form.datos[key] !== user[key]) {
            data[key] = this.form.datos[key];
          }
        });
      }
      return data;
    },

    saveNotas(id) {
      let user = this.course.estudiantes.find(student => student.id === id);

      let datos = this.cambios(user);

      if (Object.keys(datos).length > 0) {
        this.updateNotas({
          cursa_id: id,
          curso_id: this.course_id,
          datos
        }).then(() => {
          console.log("Notas guardadas del estudiante ", id);
        });
      }
    }
  },
  mounted() {
      this.course_id = this.$route.params.id;
  }
};
</script>

