// .AXIOS

// BLOCKUI
import BlockUI from "vue-blockui";
Vue.use(BlockUI);
// .BLOCKUI

// VEE-VALIDATE
import VeeValidate, {
  Validator
} from 'vee-validate';

import validatorEs from './validator/es';

Vue.use(VeeValidate);

Validator.localize('es', validatorEs);
// .VEE-VALIDATE

// Vue Traducciones
import VueI18n from 'vue-i18n';
Vue.use(VueI18n);
import messages from '@/translations';
const i18n = new VueI18n({
  locale: store.state.global.language,
  messages
});
// .Vue Traducciones

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
