<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
      <v-toolbar class="primary--text" color="transparent" center card dark flat>
        <v-spacer></v-spacer>
        <!-- <v-icon>person</v-icon> -->
        <v-toolbar-title>{{ $t('new.admin') }}</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>

      <form ref="form" autocomplete="false" @submit.prevent="validateBeforeSubmit">
        <v-card-text>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="names"
              ref="nombres"
              name="names"
              v-validate="'required|min:3'"
              :error-messages="errors.first('names')"
              data-vv-name="names"
              append-icon="person"
              :label="$t('new.names')"
              solo
            ></v-text-field>
          </v-flex>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="lastname"
              ref="apellidos"
              name="lastname"
              v-validate="'required|min:3'"
              :error-messages="errors.first('lastname')"
              data-vv-name="lastname"
              append-icon="person_outline"
              :label="$t('new.lastname')"
              solo
            ></v-text-field>
          </v-flex>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="email"
              ref="correo"
              name="email"
              v-validate="'required|email'"
              :error-messages="errors.first('email')"
              data-vv-name="email"
              append-icon="mail"
              :label="$t('new.email')"
              solo
            ></v-text-field>
          </v-flex>
          <v-layout>
            <v-flex xs6>
              <v-text-field
                autocomplete="off"
                autofocus
                v-model="phone"
                ref="telf"
                name="phone"
                append-icon="call"
                :label="$t('new.phone')"
                solo
              ></v-text-field>
            </v-flex>
            <v-flex xs6>
              <v-text-field
                autocomplete="off"
                autofocus
                v-model="mobile"
                ref="cel"
                name="mobile"
                v-validate="'required'"
                :error-messages="errors.first('mobile')"
                data-vv-name="mobile"
                append-icon="phone_android"
                :label="$t('new.mobile')"
                solo
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-flex xs12 class="pa-0 ma-0">
            <v-text-field
              autocomplete="off"
              autofocus
              v-model="address"
              ref="dir"
              name="address"
              v-validate="'required'"
              :error-messages="errors.first('address')"
              data-vv-name="address"
              append-icon="place"
              :label="$t('new.address')"
              solo
            ></v-text-field>
          </v-flex>
          <v-flex xs12 class="pa-0 ma-0">
            <v-select
              :items="branch_offices"
              item-value="id"
              item-text="nombre"
              v-model="branch_office"
              v-validate="'required'"
              ref="sucursal_id"
              :error-messages="errors.first('branch_office')"
              data-vv-name="branch_office"
              :label="$t('new.branch_office')"
              solo
            ></v-select>
          </v-flex>
          <v-flex xs12>
            <v-radio-group
              color="indigo"
              v-validate="'required'"
              :error-messages="errors.first('genre')"
              data-vv-name="genre"
              :label="$t('new.genre')"
              v-model="genre"
              ref="sexo"
              prepend-icon="wc"
              dark
              column
              class="pa-0 ma-0"
            >
              <v-radio
                label="Mujer"
                dark
                on-icon="check_box"
                off-icon="check_box_outline_blank"
                value="F"
              ></v-radio>
              <v-spacer></v-spacer>
              <v-radio
                label="Varon"
                on-icon="check_box"
                off-icon="check_box_outline_blank"
                value="M"
              ></v-radio>
            </v-radio-group>
          </v-flex>
          <!-- <v-alert :value="error" color="error" icon="warning">{{ $t('login.error') }}</v-alert> -->
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-slide-x-reverse-transition>
            <v-tooltip left>
              <v-btn slot="activator" icon class="my-0" @click="resetForm">
                <v-icon color="primary">refresh</v-icon>
              </v-btn>
              <span>Refresh form</span>
            </v-tooltip>
          </v-slide-x-reverse-transition>
          <v-btn type="submit" color="primary" flat>{{ $t('new.submit') }}</v-btn>
        </v-card-actions>
      </form>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      names: "",
      lastname: "",
      email: "",
      address: "",
      phone: "",
      mobile: "",
      genre: "",
      branch_office: null
    };
  },
  computed: {
    ...mapGetters({
      branch_offices: "branch_office/branch_offices"
    }),
    form() {
      return {
        nombres: this.names,
        apellidos: this.lastname,
        correo: this.email,
        dir: this.address,
        telf: this.phone,
        cel: this.mobile,
        sexo: this.genre,
        sucursal_id: this.branch_office
      };
    }
  },
  methods: {
    ...mapActions({
      fetchBranchOffices: "branch_office/fetchBranchOffices",
      registerAdministrator: "administrator/registerAdministrator"
    }),
    resetForm() {
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
      this.$validator.reset();
    },

    validateBeforeSubmit() {
      this.$validator
        .validateAll()
        .then(result => {
          if (!result) {
            // fallan las validaciones
            // console.log('Algo anda mal', result);
          } else {
            this.registerAdministrator(this.form)
            .then(() => {
              this.$router.push({name: 'list-administrators'})
                this.resetForm();
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  mounted() {
    this.fetchBranchOffices();
  }
};
</script>

