import Vue from 'vue'
import Router from 'vue-router'

// Global store
import store from "@/store";
// .Global store

import { axios } from "./main";

// components
import Home from "./views/Home";
import Login from "./views/Auth/Login";
import Profile from "./views/Auth/Profile";
import Teachers from "./views/Teacher/Teachers";
import NewTeacher from "./views/Teacher/NewTeacher";
import ListTeachers from "./views/Teacher/ListTeachers";
import UpdateTeacher from "./views/Teacher/UpdateTeacher";
import BranchOffice from "./views/BranchOffice/BranchOffice";
import NewBranchOffice from "./views/BranchOffice/NewBranchOffice";
import ListBranchOffice from "./views/BranchOffice/ListBranchOffice";
import UpdateBranchOffice from "./views/BranchOffice/UpdateBranchOffice";
import Administrator from "./views/Administrator/Administrator";
import NewAdministrator from "./views/Administrator/NewAdministrator";
import ListAdministrators from "./views/Administrator/ListAdministrators";
import UpdateAdministrator from "./views/Administrator/UpdateAdministrator";

import Book from "./views/Book/Book";
import UpdateBook from "./views/Book/UpdateBook";
import ListBooks from "./views/Book/ListBooks";

import Level from "./views/Level/Level";
import UpdateLevel from "./views/Level/UpdateLevel";
import ListLevels from "./views/Level/ListLevels";

import Student from "./views/Student/Student";
import NewStudent from "./views/Student/NewStudent";
import EnrollStudent from "./views/Student/EnrollStudent";
import SearchStudent from "./views/Student/SearchStudent";
import UpdateStudent from "./views/Student/UpdateStudent";
import ListStudents from "./views/Student/ListStudents";
import QualifyStudents from "./views/Student/QualifyStudents";

import Qualify from "./views/Teacher/Qualify";

import History from "./views/History/History";
import ShowCourse from "./views/History/ShowCourse";
import ConsultaNota from "./views/History/ConsultaNota";

// .components

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      redirect: 'login',
      component: Home,
      meta: { Auth: true, title: 'Home' },
    },
    {
      path: '/qualify',
      name: 'course-qualify',
      component: Qualify,
      meta: { Auth: true, Role: 'docente', title: 'Calificar' },
    },
    {
      path: '/consult',
      name: 'consult',
      component: ConsultaNota,
      meta: { Auth: false, title: 'Consulta Nota' },
    },
    {
      path: '/course/:id',
      name: 'course',
      component: ShowCourse,
      meta: { Auth: true, Role: 'admin', title: 'Ver Curso' },
    },
    {
      path: "/login",
      name: "login",
      component: Login,
      meta: { Auth: false, title: 'Iniciar sesin' },
      beforeEnter: (to, from, next) => {
        if (store.state.auth.logged) {
          if (store.state.auth.role === "root") {
            next({ path: "/branch_office/list" });
          } else if (
            store.state.auth.role === "admin"
          ) {
            next({ path: "/student/qualify" });
          } else if (store.state.auth.role === "docente") {
            next({ path: "/qualify" });
          }
        } else {
          next();
        }
      }
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile,
      meta: { Auth: true, Role: '', title: 'Perfil' },
    },
    {
      path: '/history',
      name: 'history',
      component: History,
      meta: { Auth: true, Role: 'admin', title: 'Historial' },
    },
    {
      path: '/admin',
      name: 'administrator',
      component: Administrator,
      redirect: 'admin/new',
      children: [
        {
          path: 'new',
          name: 'new-administrator',
          component: NewAdministrator,
          meta: { Auth: true, Role: 'root', title: 'Nuevo Administrador' },
        },
        {
          path: 'list',
          name: 'list-administrators',
          component: ListAdministrators,
          meta: { Auth: true, Role: 'root', title: 'Lista de Administradores' },
        },
        {
          path: 'update/:id',
          name: 'update-administrator',
          component: UpdateAdministrator,
          meta: { Auth: true, Role: 'root', title: 'Actualizar Administrador' },
        }
      ]
    },
    {
      path: '/teacher',
      name: 'teacher',
      component: Teachers,
      redirect: 'teachers/list',
      children: [
        {
          path: 'new',
          name: 'new-teacher',
          component: NewTeacher,
          meta: { Auth: true, Role: 'admin', title: 'Nuevo Docente' },
        },
        {
          path: 'list',
          name: 'list-docentes',
          component: ListTeachers,
          meta: { Auth: true, Role: 'admin', title: 'Lista de Docente' },
        },
        {
          path: 'update/:id',
          name: 'update-teacher',
          component: UpdateTeacher,
          meta: { Auth: true, Role: 'admin', title: 'Actualizar Administrador' },
        }
      ]
    },
    {
      path: '/student',
      name: 'student',
      component: Student,
      redirect: 'student/search',
      children: [
        {
          path: 'new',
          name: 'new-student',
          component: NewStudent,
          meta: { Auth: true, Role: 'admin', title: 'Inscribir estudiante' },
        },
        {
          path: 'enroll',
          name: 'enroll-student',
          component: EnrollStudent,
          meta: { Auth: true, Role: 'admin', title: 'Inscribir estudiante' },
        },
        {
          path: 'qualify',
          name: 'quelify-students',
          component: QualifyStudents,
          meta: { Auth: true, Role: 'admin', title: 'Calificar Estudiantes' },
        },
        {
          path: 'search',
          name: 'search-student',
          component: SearchStudent,
          meta: { Auth: true, Role: 'admin', title: 'Buscar estudiante' },
        },
        {
          path: 'list',
          name: 'list-students',
          component: ListStudents,
          meta: { Auth: true, Role: 'admin', title: 'Lista de Estudiantes' },
        },
        {
          path: 'update/:id',
          name: 'update-student',
          component: UpdateStudent,
          meta: { Auth: true, Role: 'admin', title: 'Actualizar Estudiante' },
        }
      ]
    },
    {
      path: '/branch_office',
      name: 'branch-office',
      component: BranchOffice,
      redirect: 'branch_office/list',
      children: [
        // {
        //   path: 'new',
        //   name: 'new-branch-office',
        //   component: NewBranchOffice,
        //   meta: { Auth: true, Role: 'root', title: 'Nuevo Sucursal' },
        // },
        {
          path: 'list',
          name: 'list-branch-offices',
          component: ListBranchOffice,
          meta: { Auth: true, Role: 'root', title: 'Lista de Sucursales' },
        },
        {
          path: 'update/:id',
          name: 'update-branch-office',
          component: UpdateBranchOffice,
          meta: { Auth: true, Role: 'root', title: 'Actualizar Sucursal' },
        }
      ]
    },
    {
      path: '/book',
      name: 'book',
      component: Book,
      redirect: 'book/list',
      children: [
        {
          path: 'list',
          name: 'list-books',
          component: ListBooks,
          meta: { Auth: true, Role: 'admin', title: 'Lista de Libros' },
        },
        {
          path: 'update/:id',
          name: 'update-book',
          component: UpdateBook,
          meta: { Auth: true, Role: 'admin', title: 'Actualizar Libro' },
        }
      ]
    },
    {
      path: '/level',
      name: 'level',
      component: Level,
      redirect: 'level/list',
      children: [
        {
          path: 'list',
          name: 'list-levels',
          component: ListLevels,
          meta: { Auth: true, Role: 'root', title: 'Lista de Niveles' },
        },
        {
          path: 'update/:id',
          name: 'update-level',
          component: UpdateLevel,
          meta: { Auth: true, Role: 'root', title: 'Actualizar Nivel' },
        }
      ]
    },
  ]
})

// para cada cambio de ruta
router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  axios.defaults.headers = {
    Authorization: localStorage.getItem('_token'),
    'Content-Type': 'application/json',
  };
  if (to.meta.Auth && !store.state.auth.logged) {
    next({ path: '/login' });
  } else {
    if (store.state.auth.logged) {
      store.commit('auth/setUser');
    }
    if (store.state.auth.role === to.meta.Role || to.path === '/login' || to.path === '/') {
      next();
    } else {
      if (!to.meta.Auth && to.path === '/consult') {
        next();
      } else {
        if (to.meta.Role === '' && to.meta.Auth) {
          next();
        } else {
          next(from.path);
        }
      }
    }
  }
});
// .para cada cambio de ruta

export default router;
