<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
        <v-toolbar class="primary--text" color="transparent" center card dark flat>
      <v-spacer></v-spacer>
      <!-- <v-icon>person</v-icon> -->
      <v-toolbar-title>Actualizar datos del Estudiante</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
      <update-user
        :form="form"
        :cambios="cambios"
        :update="true"
        v-on:updateRegister="nowUpdateStudent()"
      ></update-user>
    </v-flex>
  </v-layout>
</template>

<script>
import UpdateUser from "@/components/Form/UpdateUser.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      names: "",
      lastname: "",
      ci: "",
      mobile: "",
      email: "",
      genre: "",
      user: null
    };
  },
  computed: {
    ...mapGetters({
      list_enabled: "book/list_enabled"
    }),
    form() {
      return {
        nombres: this.names,
        apellidos: this.lastname,
        correo: this.email,
        ci: this.ci,
        cel: this.mobile,
        sexo: this.genre
      };
    }
  },
  methods: {
    ...mapActions({
      updateStudent: "student/updateStudent"
    }),
    cambios() {
      let data = {};
      if (Object.keys(this.form).length > 0) {
        Object.keys(this.form).forEach(key => {
          if (!!this.form[key] && this.form[key] !== this.user[key]) {
            data[key] = this.form[key];
          }
        });
      }
      return data;
    },
    resetForm() {
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
      this.$validator.reset();
    },

    nowUpdateStudent() {
      this.updateStudent({
        id: this.$route.params.id,
        datos: this.cambios()
      }).then(() => {
        // console.log("Lo registroooo");
        this.$router.push({name: 'list-students'})
      });
    }
  },
  mounted() {
    console.log(this.$route.params);
    if (this.$route.params.nombres) {
      this.user = this.$route.params;
      this.names = this.$route.params.nombres;
      this.lastname = this.$route.params.apellidos;
      this.ci = this.$route.params.ci;
      this.genre = this.$route.params.sexo;
      this.mobile = this.$route.params.cel;
      this.email = this.$route.params.correo;
    } else {
      this.$router.push({ name: "list-students" });
    }
  }
};
</script>

