/* Axios */
import { axios } from "./../../main";

const student = {
    namespaced: true,

    state: {
        students: [],
        headers: [],
        query: {
            search: ''
        }
    },

    getters: {
        students: (state) => state.students.sort((curso1, curso2) => {
            if (curso1.nro > curso2.nro) {
                return 1;
            } else if (curso1.nro < curso2.nro) {
                return -1;
            }
            return 0;
        }),
        headers: (state) => state.headers,
    },

    actions: {
        registerStudent: ({ commit }, student) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                axios.post('estudiante/inscribe', { datos: student })
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log('student', data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        enrollStudent: ({ commit }, student) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                console.log(student);

                axios.post('estudiante/inscribeantiguo', { datos: student })
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log('student', data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        consultarNota: ({ commit }, ci) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                console.log({ ci });

                axios.post('notas', { ci })
                    .then((data) => {
                        if (data.data.code === 200) {
                            // localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },



        searchStudent: ({ commit }, student) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                console.log({ datos: student });

                axios.post('estudiante/consulta', { datos: student })
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        updateStudent: ({ commit }, student) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {
                console.log(student);

                axios.post('estudiante/', student)
                    .then((data) => {

                        console.log('Update', data);
                        if (data.data.code === 200) {
                            commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },
        deleteStudent: ({ commit }, id) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                axios.post('estudiante/borrar', { id })
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log(data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        fetchStudents: ({ commit }) => {
            return new Promise((resolve, reject) => {
                axios.get('estudiante/')
                    .then((data) => {
                        console.log(data);

                        if (data.data.code === 200) {
                            commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        },
        updateNotas: ({ commit }, notas) => {
            commit('global/startProcessing', null, { root: true });
            return new Promise((resolve, reject) => {

                console.log(notas);

                axios.post('estudiante/nota', notas)
                    .then((data) => {

                        console.log('Update', data);
                        if (data.data.code === 200) {
                            // commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        console.log('UploadNotas', data);

                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    }).catch((err) => {
                        reject(err);
                    }).finally(() => {
                        commit('global/stopProcessing', null, { root: true });
                    });
            });
        },

        fetchNotas: ({ commit }, id) => {
            return new Promise((resolve, reject) => {
                axios.post('estudiante/notas', { id })
                    .then((data) => {
                        if (data.data.code === 200) {
                            // commit('receivedStudents', data);
                            localStorage.setItem('_token', data.data.content.token);
                        }
                        commit('global/setMessage', data.data.usrmsg, { root: true });
                        commit('global/setSuccess', data.data.code === 200, { root: true });
                        resolve(data);
                    })
                    .catch((err) => {
                        console.log(err);
                        reject(err);
                    })
            })
        }
    },

    mutations: {
        receivedStudents: (state, apiResponse) => {
            state.students = apiResponse.data.content.data.cursos;
            let headers = [];
            const curso = state.students.find((curso) => curso.estudiantes.length > 0);
            if (curso) {
                for (const key in curso.estudiantes[0]) {
                    let ingresa = false;
                    let admin = {};
                    switch (key) {
                        case 'nombres':
                            ingresa = true;
                            admin.text = 'Nombres';
                            break;
                        case 'apellidos':
                            ingresa = true;
                            admin.text = 'Apellidos';
                            break;
                        case 'ci':
                            ingresa = true;
                            admin.text = 'Cdula de Identidad';
                            admin.sortable = false;
                            break;
                        case 'cel':
                            ingresa = true;
                            admin.text = 'Celular'
                            admin.sortable = false;
                            break;
                        default:
                            break;
                    }
                    admin.value = key;
                    admin.align = 'center';
                    if (ingresa) {
                        headers.push(admin);
                    }
                }
                headers.push({
                    text: 'Acciones',
                    value: 'botones',
                    sortable: false,
                })
            }


            state.headers = headers;

        },
    }
};

export default student;