<template>
  <v-layout justify-center>
    <v-flex xs12 sm12 md12 lg12>
      <users-list :item_list="teachers" title="Docentes" :search="search" :headers="headers" v-on:removeUser="removeTeacher($event)" path="update-teacher"></users-list>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import UsersList from "@/components/List/UsersList.vue";
export default {
  name: "list-teachers",
  components: {
    UsersList
  },
  data() {
    return {
      search: ""
    };
  },
  computed: {
    ...mapGetters({
      teachers: "teacher/teachers",
      headers: "teacher/headers"
    })
  },
  methods: {
    ...mapActions({
      fetchTeachers: "teacher/fetchTeachers",
      deleteTeacher: "teacher/deleteTeacher"
    }),

    removeTeacher(id) {
      this.$swal({
        title: "Ests seguro?",
        text: "",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "S, hazlo!"
      }).then(result => {
        if (result.value) {
          this.deleteTeacher(id);
        }
      });
    }
  },
  mounted() {
    this.fetchTeachers();
  }
};
</script>

